import type { BiFactory } from '@ali/4ever-cangjie';
import { key } from '../utils/meta';
import toolbar from './toolbar';
import onAction from './handlers/onAction';
import createOnCangjieInput from './handlers/onCangjieInput';
import createRenderToolbar from './createRenderToolbar';
import { commands } from './commands';
import { queries } from './queries';
import { actions, isActions } from './actions';

export const biFactory: BiFactory = (configs) => {
  const highlightConfig = configs.highlight || {};

  return {
    key,
    toolbar: toolbar(highlightConfig),
    renderToolbar: createRenderToolbar(highlightConfig.locale),
    commands,
    queries,
    actions,
    isActions,
    onAction,
    onCangjieInput: createOnCangjieInput(),
    models: {
      klass: 'mark',
      type: 'highlight',
    },
  };
};
